import React, { useState } from 'react'
import { motion } from 'framer-motion'
import { Search, SlidersHorizontal, ShoppingCart, Heart, ArrowLeft, Sparkles } from 'lucide-react'
import { Link } from 'react-router-dom'

const mockProducts = Array.from({ length: 12 }).map((_, i) => ({
  id: `${i + 1}`,
  name: `Product ${i + 1}`,
  price: (29 + i).toFixed(2),
  image: '/api/placeholder/400/500',
  tags: i % 2 === 0 ? ['New'] : ['Popular']
}))

const ShopPage: React.FC = () => {
  const [query, setQuery] = useState('')
  const [favorites, setFavorites] = useState<Record<string, boolean>>({})

  const filtered = mockProducts.filter(p => p.name.toLowerCase().includes(query.toLowerCase()))

  return (
    <div className="min-h-screen bg-gradient-to-br from-primary-50 via-white to-secondary-50 py-8">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <div className="flex items-center justify-between mb-8">
          <Link to="/" className="flex items-center space-x-2 text-gray-600 hover:text-primary-600 transition-colors duration-200">
            <ArrowLeft className="w-5 h-5" />
            <span>Back to Home</span>
          </Link>
          <div className="flex items-center space-x-2">
            <div className="w-8 h-8 bg-gradient-to-br from-primary-500 to-secondary-500 rounded-lg flex items-center justify-center">
              <Sparkles className="w-5 h-5 text-white" />
            </div>
            <span className="text-xl font-bold text-gradient">Smart Shopping</span>
          </div>
        </div>

        <div className="flex items-center gap-3 mb-6">
          <div className="flex-1 relative">
            <Search className="absolute left-3 top-1/2 -translate-y-1/2 w-5 h-5 text-gray-400" />
            <input value={query} onChange={(e) => setQuery(e.target.value)} placeholder="Search products" className="w-full pl-10 pr-4 py-3 rounded-lg border border-gray-200 focus:border-primary-400 focus:ring-0 outline-none" />
          </div>
          <button className="btn-outline flex items-center gap-2">
            <SlidersHorizontal className="w-5 h-5" />
            Filters
          </button>
        </div>

        <div className="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-6">
          {filtered.map((p, index) => (
            <motion.div key={p.id} initial={{ opacity: 0, y: 20 }} whileInView={{ opacity: 1, y: 0 }} viewport={{ once: true }} transition={{ duration: 0.4, delay: index * 0.05 }} className="card overflow-hidden group">
              <div className="relative">
                <img src={p.image} alt={p.name} className="w-full h-56 object-cover" />
                <button onClick={() => setFavorites(prev => ({ ...prev, [p.id]: !prev[p.id] }))} className={`absolute top-3 right-3 w-9 h-9 rounded-full flex items-center justify-center ${favorites[p.id] ? 'bg-red-500 text-white' : 'bg-white/90 text-gray-700 hover:bg-white'}`}>
                  <Heart className="w-5 h-5" />
                </button>
                {p.tags?.[0] && (
                  <span className="absolute bottom-3 left-3 text-xs bg-black/70 text-white px-2 py-1 rounded">
                    {p.tags[0]}
                  </span>
                )}
              </div>
              <div className="p-4">
                <div className="font-semibold text-gray-900 mb-1">{p.name}</div>
                <div className="text-primary-700 font-bold">${p.price}</div>
                <button className="btn-primary w-full mt-3 flex items-center justify-center gap-2">
                  <ShoppingCart className="w-5 h-5" />
                  Add to Cart
                </button>
              </div>
            </motion.div>
          ))}
        </div>
      </div>
    </div>
  )
}

export default ShopPage


