import React, { useState, useRef } from 'react'
import { motion } from 'framer-motion'
import { 
  Camera, 
  Ruler, 
  Upload, 
  RotateCcw, 
  Download, 
  Share2, 
  ArrowLeft,
  Sparkles,
  Zap,
  Target,
  CheckCircle,
  AlertCircle
} from 'lucide-react'
import { Link } from 'react-router-dom'

const MeasurementPage: React.FC = () => {
  const [isCameraActive, setIsCameraActive] = useState(false)
  const [capturedImage, setCapturedImage] = useState<string | null>(null)
  const [isProcessing, setIsProcessing] = useState(false)
  const [measurements, setMeasurements] = useState<any>(null)
  const [measurementHistory, setMeasurementHistory] = useState<any[]>([])
  const videoRef = useRef<HTMLVideoElement>(null)
  const canvasRef = useRef<HTMLCanvasElement>(null)

  const measurementTypes = [
    { key: 'chest', label: 'Chest', unit: 'cm', icon: Target },
    { key: 'waist', label: 'Waist', unit: 'cm', icon: Target },
    { key: 'hips', label: 'Hips', unit: 'cm', icon: Target },
    { key: 'shoulders', label: 'Shoulders', unit: 'cm', icon: Target },
    { key: 'inseam', label: 'Inseam', unit: 'cm', icon: Target },
    { key: 'height', label: 'Height', unit: 'cm', icon: Target }
  ]

  const startCamera = async () => {
    try {
      const stream = await navigator.mediaDevices.getUserMedia({ 
        video: { 
          facingMode: 'user',
          width: { ideal: 1280 },
          height: { ideal: 720 }
        } 
      })
      if (videoRef.current) {
        videoRef.current.srcObject = stream
        setIsCameraActive(true)
      }
    } catch (error) {
      console.error('Error accessing camera:', error)
    }
  }

  const stopCamera = () => {
    if (videoRef.current && videoRef.current.srcObject) {
      const stream = videoRef.current.srcObject as MediaStream
      stream.getTracks().forEach(track => track.stop())
      setIsCameraActive(false)
    }
  }

  const capturePhoto = () => {
    if (videoRef.current && canvasRef.current) {
      const context = canvasRef.current.getContext('2d')
      if (context) {
        canvasRef.current.width = videoRef.current.videoWidth
        canvasRef.current.height = videoRef.current.videoHeight
        context.drawImage(videoRef.current, 0, 0)
        const imageData = canvasRef.current.toDataURL('image/jpeg')
        setCapturedImage(imageData)
        stopCamera()
      }
    }
  }

  const retakePhoto = () => {
    setCapturedImage(null)
    setMeasurements(null)
  }

  const processMeasurements = async () => {
    if (!capturedImage) return
    
    setIsProcessing(true)
    
    // Simulate AI processing
    setTimeout(() => {
      const mockMeasurements = {
        chest: 95.5,
        waist: 78.2,
        hips: 98.7,
        shoulders: 45.3,
        inseam: 78.9,
        height: 175.0,
        timestamp: new Date().toISOString(),
        accuracy: 94.2
      }
      setMeasurements(mockMeasurements)
      
      // Add to history
      setMeasurementHistory(prev => [mockMeasurements, ...prev.slice(0, 4)])
      setIsProcessing(false)
    }, 4000)
  }

  const handleFileUpload = (event: React.ChangeEvent<HTMLInputElement>) => {
    const file = event.target.files?.[0]
    if (file) {
      const reader = new FileReader()
      reader.onload = (e) => {
        setCapturedImage(e.target?.result as string)
      }
      reader.readAsDataURL(file)
    }
  }

  const getSizeRecommendation = (measurements: any) => {
    if (!measurements) return null
    
    // Simple size logic (in real app, this would be more sophisticated)
    const chest = measurements.chest
    if (chest < 85) return 'S'
    if (chest < 95) return 'M'
    if (chest < 105) return 'L'
    return 'XL'
  }

  const exportMeasurements = () => {
    if (!measurements) return
    
    const dataStr = JSON.stringify(measurements, null, 2)
    const dataBlob = new Blob([dataStr], { type: 'application/json' })
    const url = URL.createObjectURL(dataBlob)
    const link = document.createElement('a')
    link.href = url
    link.download = `measurements-${new Date().toISOString().split('T')[0]}.json`
    link.click()
    URL.revokeObjectURL(url)
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-primary-50 via-white to-secondary-50 py-8">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        {/* Header */}
        <div className="flex items-center justify-between mb-8">
          <Link
            to="/"
            className="flex items-center space-x-2 text-gray-600 hover:text-primary-600 transition-colors duration-200"
          >
            <ArrowLeft className="w-5 h-5" />
            <span>Back to Home</span>
          </Link>
          
          <div className="flex items-center space-x-2">
            <div className="w-8 h-8 bg-gradient-to-br from-primary-500 to-secondary-500 rounded-lg flex items-center justify-center">
              <Sparkles className="w-5 h-5 text-white" />
            </div>
            <span className="text-xl font-bold text-gradient">Body Measurement</span>
          </div>
        </div>

        <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">
          {/* Left Panel - Camera/Photo */}
          <div className="lg:col-span-2">
            <motion.div
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ duration: 0.6 }}
              className="card p-6"
            >
              <div className="flex items-center justify-between mb-6">
                <h2 className="text-2xl font-bold text-gray-900">Take Measurement Photo</h2>
                <div className="flex items-center space-x-2">
                  <Zap className="w-5 h-5 text-yellow-500" />
                  <span className="text-sm text-gray-600">AI-Powered</span>
                </div>
              </div>

              {!capturedImage ? (
                <div className="space-y-6">
                  {/* Camera View */}
                  {isCameraActive ? (
                    <div className="relative">
                      <video
                        ref={videoRef}
                        autoPlay
                        playsInline
                        className="w-full h-96 object-cover rounded-lg"
                      />
                      <div className="absolute inset-0 flex items-center justify-center">
                        <div className="w-64 h-80 border-2 border-primary-500 border-dashed rounded-lg flex items-center justify-center">
                          <div className="text-center text-primary-600">
                            <Target className="w-8 h-8 mx-auto mb-2" />
                            <span className="font-medium">Position yourself here</span>
                            <p className="text-sm mt-1">Wear form-fitting clothes</p>
                          </div>
                        </div>
                      </div>
                    </div>
                  ) : (
                    <div className="w-full h-96 bg-gradient-to-br from-gray-100 to-gray-200 rounded-lg flex items-center justify-center border-2 border-dashed border-gray-300">
                      <div className="text-center">
                        <Ruler className="w-16 h-16 text-gray-400 mx-auto mb-4" />
                        <p className="text-gray-600 mb-4">Ready to measure your body</p>
                        <button
                          onClick={startCamera}
                          className="btn-primary flex items-center space-x-2 mx-auto"
                        >
                          <Camera className="w-5 h-5" />
                          <span>Start Camera</span>
                        </button>
                      </div>
                    </div>
                  )}

                  {/* Camera Controls */}
                  {isCameraActive && (
                    <div className="flex justify-center space-x-4">
                      <button
                        onClick={capturePhoto}
                        className="w-16 h-16 bg-primary-600 hover:bg-primary-700 rounded-full flex items-center justify-center text-white transition-colors duration-200"
                      >
                        <Camera className="w-8 h-8" />
                      </button>
                      <button
                        onClick={stopCamera}
                        className="w-12 h-12 bg-gray-600 hover:bg-gray-700 rounded-full flex items-center justify-center text-white transition-colors duration-200"
                      >
                        <RotateCcw className="w-6 h-6" />
                      </button>
                    </div>
                  )}

                  {/* File Upload Alternative */}
                  <div className="text-center">
                    <p className="text-gray-600 mb-2">Or upload a photo</p>
                    <label className="btn-outline cursor-pointer inline-flex items-center space-x-2">
                      <Upload className="w-5 h-5" />
                      <span>Choose File</span>
                      <input
                        type="file"
                        accept="image/*"
                        onChange={handleFileUpload}
                        className="hidden"
                      />
                    </label>
                  </div>
                </div>
              ) : (
                <div className="space-y-6">
                  {/* Captured Photo */}
                  <div className="relative">
                    <img
                      src={capturedImage}
                      alt="Captured photo"
                      className="w-full h-96 object-cover rounded-lg"
                    />
                    <button
                      onClick={retakePhoto}
                      className="absolute top-4 right-4 w-10 h-10 bg-white/90 hover:bg-white rounded-full flex items-center justify-center text-gray-700 hover:text-primary-600 transition-colors duration-200"
                    >
                      <RotateCcw className="w-5 h-5" />
                    </button>
                  </div>

                  {/* Process Button */}
                  <button
                    onClick={processMeasurements}
                    disabled={isProcessing}
                    className="w-full btn-primary py-4 text-lg disabled:opacity-50 disabled:cursor-not-allowed"
                  >
                    {isProcessing ? (
                      <div className="flex items-center justify-center space-x-2">
                        <div className="w-5 h-5 border-2 border-white border-t-transparent rounded-full animate-spin"></div>
                        <span>Processing Measurements...</span>
                      </div>
                    ) : (
                      <span>Process Measurements</span>
                    )}
                  </button>
                </div>
              )}

              {/* Measurement Results */}
              {measurements && (
                <motion.div
                  initial={{ opacity: 0, scale: 0.95 }}
                  animate={{ opacity: 1, scale: 1 }}
                  transition={{ duration: 0.5 }}
                  className="mt-6"
                >
                  <div className="flex items-center justify-between mb-4">
                    <h3 className="text-xl font-semibold text-gray-900">Your Measurements</h3>
                    <div className="flex items-center space-x-2">
                      <CheckCircle className="w-5 h-5 text-green-500" />
                      <span className="text-sm text-green-600 font-medium">
                        {measurements.accuracy}% Accuracy
                      </span>
                    </div>
                  </div>

                  <div className="grid grid-cols-2 md:grid-cols-3 gap-4 mb-6">
                    {measurementTypes.map((type) => {
                      const Icon = type.icon
                      return (
                        <div key={type.key} className="bg-gray-50 rounded-lg p-4 text-center">
                          <Icon className="w-6 h-6 text-primary-600 mx-auto mb-2" />
                          <div className="text-2xl font-bold text-gray-900">
                            {measurements[type.key]}
                          </div>
                          <div className="text-sm text-gray-600">{type.unit}</div>
                          <div className="text-xs text-gray-500 mt-1">{type.label}</div>
                        </div>
                      )
                    })}
                  </div>

                  {/* Size Recommendation */}
                  <div className="bg-primary-50 rounded-lg p-4 mb-4">
                    <div className="flex items-center justify-between">
                      <div>
                        <h4 className="font-semibold text-primary-900">Recommended Size</h4>
                        <p className="text-primary-700">
                          Based on your measurements, we recommend size{' '}
                          <span className="font-bold text-lg">{getSizeRecommendation(measurements)}</span>
                        </p>
                      </div>
                      <div className="text-right">
                        <div className="text-sm text-primary-600">Last Updated</div>
                        <div className="text-xs text-primary-500">
                          {new Date(measurements.timestamp).toLocaleDateString()}
                        </div>
                      </div>
                    </div>
                  </div>

                  {/* Action Buttons */}
                  <div className="flex space-x-4">
                    <button
                      onClick={exportMeasurements}
                      className="btn-outline flex-1 flex items-center justify-center space-x-2"
                    >
                      <Download className="w-5 h-5" />
                      <span>Export Data</span>
                    </button>
                    <button className="btn-secondary flex-1 flex items-center justify-center space-x-2">
                      <Share2 className="w-5 h-5" />
                      <span>Share Results</span>
                    </button>
                  </div>
                </motion.div>
              )}
            </motion.div>
          </div>

          {/* Right Panel - Instructions & History */}
          <div className="lg:col-span-1">
            {/* Instructions */}
            <motion.div
              initial={{ opacity: 0, x: 20 }}
              animate={{ opacity: 1, x: 0 }}
              transition={{ duration: 0.6, delay: 0.2 }}
              className="card p-6 mb-6"
            >
              <h3 className="text-lg font-semibold text-gray-900 mb-4">Measurement Guide</h3>
              <div className="space-y-4">
                <div className="flex items-start space-x-3">
                  <div className="w-6 h-6 bg-primary-100 rounded-full flex items-center justify-center flex-shrink-0 mt-0.5">
                    <span className="text-primary-600 text-xs font-medium">1</span>
                  </div>
                  <div>
                    <h4 className="font-medium text-gray-900">Wear Form-Fitting Clothes</h4>
                    <p className="text-sm text-gray-600">Tight clothing ensures accurate measurements</p>
                  </div>
                </div>
                
                <div className="flex items-start space-x-3">
                  <div className="w-6 h-6 bg-primary-100 rounded-full flex items-center justify-center flex-shrink-0 mt-0.5">
                    <span className="text-primary-600 text-xs font-medium">2</span>
                  </div>
                  <div>
                    <h4 className="font-medium text-gray-900">Stand Naturally</h4>
                    <p className="text-sm text-gray-600">Relax your posture, don't suck in your stomach</p>
                  </div>
                </div>
                
                <div className="flex items-start space-x-3">
                  <div className="w-6 h-6 bg-primary-100 rounded-full flex items-center justify-center flex-shrink-0 mt-0.5">
                    <span className="text-primary-600 text-xs font-medium">3</span>
                  </div>
                  <div>
                    <h4 className="font-medium text-gray-900">Good Lighting</h4>
                    <p className="text-sm text-gray-600">Ensure clear visibility of your body outline</p>
                  </div>
                </div>
                
                <div className="flex items-start space-x-3">
                  <div className="w-6 h-6 bg-primary-100 rounded-full flex items-center justify-center flex-shrink-0 mt-0.5">
                    <span className="text-primary-600 text-xs font-medium">4</span>
                  </div>
                  <div>
                    <h4 className="font-medium text-gray-900">Stay Still</h4>
                    <p className="text-sm text-gray-600">Hold your position while the photo is taken</p>
                  </div>
                </div>
              </div>
            </motion.div>

            {/* Measurement History */}
            {measurementHistory.length > 0 && (
              <motion.div
                initial={{ opacity: 0, y: 20 }}
                animate={{ opacity: 1, y: 0 }}
                transition={{ duration: 0.6, delay: 0.4 }}
                className="card p-6"
              >
                <h3 className="text-lg font-semibold text-gray-900 mb-4">Measurement History</h3>
                <div className="space-y-3">
                  {measurementHistory.map((record, index) => (
                    <div key={index} className="flex items-center justify-between p-3 bg-gray-50 rounded-lg">
                      <div>
                        <div className="font-medium text-gray-900">
                          {new Date(record.timestamp).toLocaleDateString()}
                        </div>
                        <div className="text-sm text-gray-600">
                          Chest: {record.chest}cm | Waist: {record.waist}cm
                        </div>
                      </div>
                      <div className="text-right">
                        <div className="text-sm font-medium text-green-600">
                          {record.accuracy}%
                        </div>
                        <div className="text-xs text-gray-500">Accuracy</div>
                      </div>
                    </div>
                  ))}
                </div>
                <div className="mt-4 text-right">
                  <a href="/gallery" className="text-primary-700 hover:underline">Open measurement photos</a>
                </div>
              </motion.div>
            )}

            {/* Tips */}
            <motion.div
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ duration: 0.6, delay: 0.6 }}
              className="card p-6 mt-6"
            >
              <h3 className="text-lg font-semibold text-gray-900 mb-4">Pro Tips</h3>
              <ul className="space-y-3 text-sm text-gray-600">
                <li className="flex items-start space-x-2">
                  <div className="w-2 h-2 bg-primary-500 rounded-full mt-2 flex-shrink-0"></div>
                  <span>Measurements are most accurate in the morning</span>
                </li>
                <li className="flex items-start space-x-2">
                  <div className="w-2 h-2 bg-primary-500 rounded-full mt-2 flex-shrink-0"></div>
                  <span>Keep your arms slightly away from your body</span>
                </li>
                <li className="flex items-start space-x-2">
                  <div className="w-2 h-2 bg-primary-500 rounded-full mt-2 flex-shrink-0"></div>
                  <span>Update measurements every 3-6 months</span>
                </li>
                <li className="flex items-start space-x-2">
                  <div className="w-2 h-2 bg-primary-500 rounded-full mt-2 flex-shrink-0"></div>
                  <span>Use for accurate online shopping</span>
                </li>
              </ul>
            </motion.div>
          </div>
        </div>

        {/* Hidden canvas for photo capture */}
        <canvas ref={canvasRef} className="hidden" />
      </div>
    </div>
  )
}

export default MeasurementPage
