import React, { useState, useRef } from 'react'
import { motion, AnimatePresence } from 'framer-motion'
import { 
  Camera, 
  Upload, 
  RotateCcw, 
  Download, 
  Share2, 
  Heart,
  ShoppingCart,
  ArrowLeft,
  Sparkles,
  Zap
} from 'lucide-react'
import { Link } from 'react-router-dom'

const TryOnPage: React.FC = () => {
  const [isCameraActive, setIsCameraActive] = useState(false)
  const [capturedImage, setCapturedImage] = useState<string | null>(null)
  const [selectedClothing, setSelectedClothing] = useState<string | null>(null)
  const [isProcessing, setIsProcessing] = useState(false)
  const [tryOnResult, setTryOnResult] = useState<string | null>(null)
  const videoRef = useRef<HTMLVideoElement>(null)
  const canvasRef = useRef<HTMLCanvasElement>(null)

  const clothingItems = [
    { id: '1', name: 'Casual T-Shirt', image: '/api/placeholder/200/300', category: 'Tops' },
    { id: '2', name: 'Denim Jeans', image: '/api/placeholder/200/300', category: 'Bottoms' },
    { id: '3', name: 'Summer Dress', image: '/api/placeholder/200/300', category: 'Dresses' },
    { id: '4', name: 'Hoodie', image: '/api/placeholder/200/300', category: 'Outerwear' },
    { id: '5', name: 'Formal Shirt', image: '/api/placeholder/200/300', category: 'Tops' },
    { id: '6', name: 'Sneakers', image: '/api/placeholder/200/300', category: 'Footwear' }
  ]

  const startCamera = async () => {
    try {
      const stream = await navigator.mediaDevices.getUserMedia({ 
        video: { 
          facingMode: 'user',
          width: { ideal: 1280 },
          height: { ideal: 720 }
        } 
      })
      if (videoRef.current) {
        videoRef.current.srcObject = stream
        setIsCameraActive(true)
      }
    } catch (error) {
      console.error('Error accessing camera:', error)
    }
  }

  const stopCamera = () => {
    if (videoRef.current && videoRef.current.srcObject) {
      const stream = videoRef.current.srcObject as MediaStream
      stream.getTracks().forEach(track => track.stop())
      setIsCameraActive(false)
    }
  }

  const capturePhoto = () => {
    if (videoRef.current && canvasRef.current) {
      const context = canvasRef.current.getContext('2d')
      if (context) {
        canvasRef.current.width = videoRef.current.videoWidth
        canvasRef.current.height = videoRef.current.videoHeight
        context.drawImage(videoRef.current, 0, 0)
        const imageData = canvasRef.current.toDataURL('image/jpeg')
        setCapturedImage(imageData)
        stopCamera()
      }
    }
  }

  const retakePhoto = () => {
    setCapturedImage(null)
    setTryOnResult(null)
    setSelectedClothing(null)
  }

  const processTryOn = async () => {
    if (!capturedImage || !selectedClothing) return
    
    setIsProcessing(true)
    
    // Simulate AI processing
    setTimeout(() => {
      setTryOnResult('/api/placeholder/400/600')
      setIsProcessing(false)
    }, 3000)
  }

  const handleFileUpload = (event: React.ChangeEvent<HTMLInputElement>) => {
    const file = event.target.files?.[0]
    if (file) {
      const reader = new FileReader()
      reader.onload = (e) => {
        setCapturedImage(e.target?.result as string)
      }
      reader.readAsDataURL(file)
    }
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-primary-50 via-white to-secondary-50 py-8">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        {/* Header */}
        <div className="flex items-center justify-between mb-8">
          <Link
            to="/"
            className="flex items-center space-x-2 text-gray-600 hover:text-primary-600 transition-colors duration-200"
          >
            <ArrowLeft className="w-5 h-5" />
            <span>Back to Home</span>
          </Link>
          
          <div className="flex items-center space-x-2">
            <div className="w-8 h-8 bg-gradient-to-br from-primary-500 to-secondary-500 rounded-lg flex items-center justify-center">
              <Sparkles className="w-5 h-5 text-white" />
            </div>
            <span className="text-xl font-bold text-gradient">Virtual Try-On</span>
          </div>
        </div>

        <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">
          {/* Left Panel - Camera/Photo */}
          <div className="lg:col-span-2">
            <motion.div
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ duration: 0.6 }}
              className="card p-6"
            >
              <div className="flex items-center justify-between mb-6">
                <h2 className="text-2xl font-bold text-gray-900">Take Your Photo</h2>
                <div className="flex items-center space-x-2">
                  <Zap className="w-5 h-5 text-yellow-500" />
                  <span className="text-sm text-gray-600">AI-Powered</span>
                </div>
              </div>

              {!capturedImage ? (
                <div className="space-y-6">
                  {/* Camera View */}
                  {isCameraActive ? (
                    <div className="relative">
                      <video
                        ref={videoRef}
                        autoPlay
                        playsInline
                        className="w-full h-96 object-cover rounded-lg"
                      />
                      <div className="absolute inset-0 flex items-center justify-center">
                        <div className="w-64 h-80 border-2 border-primary-500 border-dashed rounded-lg flex items-center justify-center">
                          <span className="text-primary-600 font-medium">Position yourself here</span>
                        </div>
                      </div>
                    </div>
                  ) : (
                    <div className="w-full h-96 bg-gradient-to-br from-gray-100 to-gray-200 rounded-lg flex items-center justify-center border-2 border-dashed border-gray-300">
                      <div className="text-center">
                        <Camera className="w-16 h-16 text-gray-400 mx-auto mb-4" />
                        <p className="text-gray-600 mb-4">Ready to capture your photo</p>
                        <button
                          onClick={startCamera}
                          className="btn-primary flex items-center space-x-2 mx-auto"
                        >
                          <Camera className="w-5 h-5" />
                          <span>Start Camera</span>
                        </button>
                      </div>
                    </div>
                  )}

                  {/* Camera Controls */}
                  {isCameraActive && (
                    <div className="flex justify-center space-x-4">
                      <button
                        onClick={capturePhoto}
                        className="w-16 h-16 bg-primary-600 hover:bg-primary-700 rounded-full flex items-center justify-center text-white transition-colors duration-200"
                      >
                        <Camera className="w-8 h-8" />
                      </button>
                      <button
                        onClick={stopCamera}
                        className="w-12 h-12 bg-gray-600 hover:bg-gray-700 rounded-full flex items-center justify-center text-white transition-colors duration-200"
                      >
                        <RotateCcw className="w-6 h-6" />
                      </button>
                    </div>
                  )}

                  {/* File Upload Alternative */}
                  <div className="text-center">
                    <p className="text-gray-600 mb-2">Or upload a photo</p>
                    <label className="btn-outline cursor-pointer inline-flex items-center space-x-2">
                      <Upload className="w-5 h-5" />
                      <span>Choose File</span>
                      <input
                        type="file"
                        accept="image/*"
                        onChange={handleFileUpload}
                        className="hidden"
                      />
                    </label>
                  </div>
                </div>
              ) : (
                <div className="space-y-6">
                  {/* Captured Photo */}
                  <div className="relative">
                    <img
                      src={capturedImage}
                      alt="Captured photo"
                      className="w-full h-96 object-cover rounded-lg"
                    />
                    <button
                      onClick={retakePhoto}
                      className="absolute top-4 right-4 w-10 h-10 bg-white/90 hover:bg-white rounded-full flex items-center justify-center text-gray-700 hover:text-primary-600 transition-colors duration-200"
                    >
                      <RotateCcw className="w-5 h-5" />
                    </button>
                  </div>

                  {/* Try-On Button */}
                  {selectedClothing && (
                    <button
                      onClick={processTryOn}
                      disabled={isProcessing}
                      className="w-full btn-primary py-4 text-lg disabled:opacity-50 disabled:cursor-not-allowed"
                    >
                      {isProcessing ? (
                        <div className="flex items-center justify-center space-x-2">
                          <div className="w-5 h-5 border-2 border-white border-t-transparent rounded-full animate-spin"></div>
                          <span>Processing Try-On...</span>
                        </div>
                      ) : (
                        <span>Try On Selected Item</span>
                      )}
                    </button>
                  )}
                </div>
              )}

              {/* Try-On Result */}
              {tryOnResult && (
                <motion.div
                  initial={{ opacity: 0, scale: 0.95 }}
                  animate={{ opacity: 1, scale: 1 }}
                  transition={{ duration: 0.5 }}
                  className="mt-6"
                >
                  <h3 className="text-xl font-semibold text-gray-900 mb-4">Try-On Result</h3>
                  <div className="relative">
                    <img
                      src={tryOnResult}
                      alt="Try-on result"
                      className="w-full h-96 object-cover rounded-lg"
                    />
                    <div className="absolute top-4 right-4 flex space-x-2">
                      <button className="w-10 h-10 bg-white/90 hover:bg-white rounded-full flex items-center justify-center text-gray-700 hover:text-primary-600 transition-colors duration-200">
                        <Heart className="w-5 h-5" />
                      </button>
                      <button className="w-10 h-10 bg-white/90 hover:bg-white rounded-full flex items-center justify-center text-gray-700 hover:text-primary-600 transition-colors duration-200">
                        <Download className="w-5 h-5" />
                      </button>
                      <button className="w-10 h-10 bg-white/90 hover:bg-white rounded-full flex items-center justify-center text-gray-700 hover:text-primary-600 transition-colors duration-200">
                        <Share2 className="w-5 h-5" />
                      </button>
                    </div>
                  </div>
                </motion.div>
              )}
              {!tryOnResult && capturedImage && (
                <div className="mt-6 flex justify-end">
                  <Link to="/try-on/360" className="btn-outline">Open 360 Preview</Link>
                </div>
              )}
            </motion.div>
          </div>

          {/* Right Panel - Clothing Selection */}
          <div className="lg:col-span-1">
            <motion.div
              initial={{ opacity: 0, x: 20 }}
              animate={{ opacity: 1, x: 0 }}
              transition={{ duration: 0.6, delay: 0.2 }}
              className="card p-6"
            >
              <h3 className="text-xl font-semibold text-gray-900 mb-6">Choose Clothing</h3>
              
              <div className="space-y-4">
                {clothingItems.map((item) => (
                  <motion.div
                    key={item.id}
                    whileHover={{ scale: 1.02 }}
                    whileTap={{ scale: 0.98 }}
                    className={`cursor-pointer rounded-lg border-2 transition-all duration-200 ${
                      selectedClothing === item.id
                        ? 'border-primary-500 bg-primary-50'
                        : 'border-gray-200 hover:border-primary-300'
                    }`}
                    onClick={() => setSelectedClothing(item.id)}
                  >
                    <div className="p-4">
                      <div className="w-full h-32 bg-gradient-to-br from-gray-100 to-gray-200 rounded-lg mb-3 flex items-center justify-center">
                        <span className="text-gray-500 text-sm">{item.name}</span>
                      </div>
                      <h4 className="font-medium text-gray-900 mb-1">{item.name}</h4>
                      <p className="text-sm text-gray-600">{item.category}</p>
                    </div>
                  </motion.div>
                ))}
              </div>

              {selectedClothing && (
                <motion.div
                  initial={{ opacity: 0, y: 10 }}
                  animate={{ opacity: 1, y: 0 }}
                  transition={{ duration: 0.3 }}
                  className="mt-6 p-4 bg-primary-50 rounded-lg border border-primary-200"
                >
                  <div className="flex items-center justify-between">
                    <span className="text-primary-700 font-medium">Item Selected</span>
                    <button
                      onClick={() => setSelectedClothing(null)}
                      className="text-primary-600 hover:text-primary-700 text-sm"
                    >
                      Change
                    </button>
                  </div>
                </motion.div>
              )}
            </motion.div>

            {/* Tips */}
            <motion.div
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ duration: 0.6, delay: 0.4 }}
              className="card p-6 mt-6"
            >
              <h3 className="text-lg font-semibold text-gray-900 mb-4">Tips for Best Results</h3>
              <ul className="space-y-3 text-sm text-gray-600">
                <li className="flex items-start space-x-2">
                  <div className="w-2 h-2 bg-primary-500 rounded-full mt-2 flex-shrink-0"></div>
                  <span>Ensure good lighting and clear background</span>
                </li>
                <li className="flex items-start space-x-2">
                  <div className="w-2 h-2 bg-primary-500 rounded-full mt-2 flex-shrink-0"></div>
                  <span>Stand at arm's length from camera</span>
                </li>
                <li className="flex items-start space-x-2">
                  <div className="w-2 h-2 bg-primary-500 rounded-full mt-2 flex-shrink-0"></div>
                  <span>Wear form-fitting clothes for accurate results</span>
                </li>
                <li className="flex items-start space-x-2">
                  <div className="w-2 h-2 bg-primary-500 rounded-full mt-2 flex-shrink-0"></div>
                  <span>Keep your body centered in the frame</span>
                </li>
              </ul>
            </motion.div>
          </div>
        </div>

        {/* Hidden canvas for photo capture */}
        <canvas ref={canvasRef} className="hidden" />
      </div>
    </div>
  )
}

export default TryOnPage
