import React, { useState } from 'react'
import { motion } from 'framer-motion'
import { Palette, Layers, Droplet, ArrowLeft, Sparkles } from 'lucide-react'
import { Link } from 'react-router-dom'

const CustomizationPage: React.FC = () => {
  const [color, setColor] = useState('#6d28d9')
  const [pattern, setPattern] = useState<'none' | 'stripes' | 'dots'>('none')
  const [layer, setLayer] = useState<'base' | 'accent'>('base')

  return (
    <div className="min-h-screen bg-gradient-to-br from-primary-50 via-white to-secondary-50 py-8">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <div className="flex items-center justify-between mb-8">
          <Link to="/" className="flex items-center space-x-2 text-gray-600 hover:text-primary-600 transition-colors duration-200">
            <ArrowLeft className="w-5 h-5" />
            <span>Back to Home</span>
          </Link>
          <div className="flex items-center space-x-2">
            <div className="w-8 h-8 bg-gradient-to-br from-primary-500 to-secondary-500 rounded-lg flex items-center justify-center">
              <Sparkles className="w-5 h-5 text-white" />
            </div>
            <span className="text-xl font-bold text-gradient">Style Customization</span>
          </div>
        </div>

        <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">
          <motion.div initial={{ opacity: 0, y: 20 }} animate={{ opacity: 1, y: 0 }} transition={{ duration: 0.6 }} className="card p-6 lg:col-span-2">
            <div className="flex items-center justify-between mb-6">
              <h2 className="text-2xl font-bold text-gray-900">Customize Your Style</h2>
              <div className="flex items-center space-x-2">
                <Palette className="w-5 h-5 text-purple-600" />
                <span className="text-sm text-gray-600">Live Preview</span>
              </div>
            </div>

            <div className="w-full h-[28rem] bg-gradient-to-br from-gray-100 to-gray-200 rounded-2xl flex items-center justify-center border-2 border-dashed border-gray-300">
              <div className="text-center">
                <div className="mx-auto mb-6 w-56 h-56 rounded-xl shadow-inner" style={{ background: color }} />
                <div className="text-gray-600">Pattern: <span className="font-medium capitalize">{pattern}</span>  Layer: <span className="font-medium capitalize">{layer}</span></div>
              </div>
            </div>
          </motion.div>

          <motion.div initial={{ opacity: 0, x: 20 }} animate={{ opacity: 1, x: 0 }} transition={{ duration: 0.6, delay: 0.1 }} className="card p-6">
            <h3 className="text-xl font-semibold text-gray-900 mb-4">Controls</h3>

            <div className="space-y-6">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2 flex items-center space-x-2">
                  <Droplet className="w-4 h-4 text-primary-600" />
                  <span>Color</span>
                </label>
                <input type="color" value={color} onChange={(e) => setColor(e.target.value)} className="w-full h-12 rounded-lg overflow-hidden cursor-pointer" />
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2 flex items-center space-x-2">
                  <Layers className="w-4 h-4 text-primary-600" />
                  <span>Layer</span>
                </label>
                <div className="grid grid-cols-2 gap-2">
                  {(['base', 'accent'] as const).map((l) => (
                    <button key={l} onClick={() => setLayer(l)} className={`px-4 py-2 rounded-lg border text-sm ${layer === l ? 'border-primary-500 bg-primary-50 text-primary-700' : 'border-gray-200 hover:border-primary-300'}`}>
                      {l[0].toUpperCase() + l.slice(1)}
                    </button>
                  ))}
                </div>
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">Pattern</label>
                <div className="grid grid-cols-3 gap-2">
                  {(['none', 'stripes', 'dots'] as const).map((p) => (
                    <button key={p} onClick={() => setPattern(p)} className={`px-4 py-2 rounded-lg border text-sm capitalize ${pattern === p ? 'border-primary-500 bg-primary-50 text-primary-700' : 'border-gray-200 hover:border-primary-300'}`}>
                      {p}
                    </button>
                  ))}
                </div>
              </div>

              <button className="btn-primary w-full py-3">Save Customization</button>
            </div>
          </motion.div>
        </div>
      </div>
    </div>
  )
}

export default CustomizationPage


