import React, { useState } from 'react'
import { motion } from 'framer-motion'
import { Search, ArrowLeft, Sparkles } from 'lucide-react'
import { Link } from 'react-router-dom'

const SearchPage: React.FC = () => {
  const [query, setQuery] = useState('')
  const [results, setResults] = useState<any[]>([])

  const onSearch = () => {
    setResults(query ? Array.from({ length: 6 }) : [])
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-primary-50 via-white to-secondary-50 py-8">
      <div className="max-w-5xl mx-auto px-4 sm:px-6 lg:px-8">
        <div className="flex items-center justify-between mb-8">
          <Link to="/" className="flex items-center space-x-2 text-gray-600 hover:text-primary-600 transition-colors duration-200">
            <ArrowLeft className="w-5 h-5" />
            <span>Back to Home</span>
          </Link>
          <div className="flex items-center space-x-2">
            <div className="w-8 h-8 bg-gradient-to-br from-primary-500 to-secondary-500 rounded-lg flex items-center justify-center">
              <Sparkles className="w-5 h-5 text-white" />
            </div>
            <span className="text-xl font-bold text-gradient">Search</span>
          </div>
        </div>

        <div className="relative mb-6">
          <Search className="absolute left-3 top-1/2 -translate-y-1/2 w-5 h-5 text-gray-400" />
          <input value={query} onChange={(e) => setQuery(e.target.value)} onKeyDown={(e) => e.key === 'Enter' && onSearch()} placeholder="Search items, brands, styles" className="w-full pl-10 pr-4 py-3 rounded-lg border border-gray-200 focus:border-primary-400 focus:ring-0 outline-none" />
        </div>

        {results.length === 0 ? (
          <div className="text-center text-gray-600">Try searching for "hoodie" or "summer dress"</div>
        ) : (
          <div className="grid grid-cols-2 md:grid-cols-3 gap-4">
            {results.map((_, i) => (
              <motion.div key={i} initial={{ opacity: 0, y: 20 }} whileInView={{ opacity: 1, y: 0 }} viewport={{ once: true }} className="bg-white rounded-lg border p-4">
                <div className="w-full h-40 bg-gray-100 rounded mb-2" />
                <div className="font-medium text-gray-900">Result {i + 1}</div>
                <div className="text-sm text-gray-600">Category</div>
              </motion.div>
            ))}
          </div>
        )}
      </div>
    </div>
  )
}

export default SearchPage


