import React, { useState } from 'react'
import { motion } from 'framer-motion'
import { Mail, Lock, UserPlus, LogIn, ArrowLeft, Sparkles } from 'lucide-react'
import { Link } from 'react-router-dom'

const AuthPage: React.FC = () => {
  const [mode, setMode] = useState<'signin' | 'signup' | 'forgot'>('signin')

  return (
    <div className="min-h-screen bg-gradient-to-br from-primary-50 via-white to-secondary-50 py-8">
      <div className="max-w-md mx-auto px-4 sm:px-6 lg:px-8">
        <div className="flex items-center justify-between mb-8">
          <Link to="/" className="flex items-center space-x-2 text-gray-600 hover:text-primary-600 transition-colors duration-200">
            <ArrowLeft className="w-5 h-5" />
            <span>Back to Home</span>
          </Link>
          <div className="flex items-center space-x-2">
            <div className="w-8 h-8 bg-gradient-to-br from-primary-500 to-secondary-500 rounded-lg flex items-center justify-center">
              <Sparkles className="w-5 h-5 text-white" />
            </div>
            <span className="text-xl font-bold text-gradient">Account</span>
          </div>
        </div>

        <motion.div initial={{ opacity: 0, y: 20 }} animate={{ opacity: 1, y: 0 }} transition={{ duration: 0.5 }} className="card p-6">
          <div className="flex gap-2 mb-6">
            <button onClick={() => setMode('signin')} className={`flex-1 py-2 rounded-lg border text-sm ${mode === 'signin' ? 'border-primary-500 bg-primary-50 text-primary-700' : 'border-gray-200'}`}>Sign in</button>
            <button onClick={() => setMode('signup')} className={`flex-1 py-2 rounded-lg border text-sm ${mode === 'signup' ? 'border-primary-500 bg-primary-50 text-primary-700' : 'border-gray-200'}`}>Create account</button>
            <button onClick={() => setMode('forgot')} className={`flex-1 py-2 rounded-lg border text-sm ${mode === 'forgot' ? 'border-primary-500 bg-primary-50 text-primary-700' : 'border-gray-200'}`}>Forgot</button>
          </div>

          {mode !== 'forgot' && (
            <div className="space-y-4">
              <div className="relative">
                <Mail className="absolute left-3 top-1/2 -translate-y-1/2 w-4 h-4 text-gray-400" />
                <input type="email" placeholder="Email" className="w-full pl-9 pr-4 py-3 rounded-lg border border-gray-200 focus:border-primary-400 focus:ring-0 outline-none" />
              </div>
              <div className="relative">
                <Lock className="absolute left-3 top-1/2 -translate-y-1/2 w-4 h-4 text-gray-400" />
                <input type="password" placeholder="Password" className="w-full pl-9 pr-4 py-3 rounded-lg border border-gray-200 focus:border-primary-400 focus:ring-0 outline-none" />
              </div>
              <button className="btn-primary w-full py-3 flex items-center justify-center gap-2">
                {mode === 'signin' ? <LogIn className="w-5 h-5" /> : <UserPlus className="w-5 h-5" />}
                {mode === 'signin' ? 'Sign in' : 'Create account'}
              </button>
              {mode === 'signin' && (
                <button onClick={() => setMode('forgot')} className="text-sm text-primary-700 hover:underline">
                  Forgot your password?
                </button>
              )}
            </div>
          )}

          {mode === 'forgot' && (
            <div className="space-y-4">
              <div className="relative">
                <Mail className="absolute left-3 top-1/2 -translate-y-1/2 w-4 h-4 text-gray-400" />
                <input type="email" placeholder="Enter your email" className="w-full pl-9 pr-4 py-3 rounded-lg border border-gray-200 focus:border-primary-400 focus:ring-0 outline-none" />
              </div>
              <button className="btn-primary w-full py-3">Send reset link</button>
              <button onClick={() => setMode('signin')} className="text-sm text-gray-700 hover:underline">Back to sign in</button>
            </div>
          )}
        </motion.div>
      </div>
    </div>
  )
}

export default AuthPage


